import React from 'react'
import Link from 'next/link'
import MobileHeader from './mobile/header'
import { withRouter } from 'next/router'

const LINKS = [
  {name: "Home", url: '/'},
  {name: "Product", url: '/product'},
  {name: "Retouching", url: '/retouching'},
  {name: "Architecture", url: '/architecture'},
  {name: "Contact", url: '/contact'},
]

export default class Header extends React.Component {
  render() {
    return(
      <div>
        <MobileHeader links={LINKS} />
        <DesktopHeader links={LINKS} />
      </div>
    )
  }
}

class DesktopHeader extends React.Component {
  styles = {
    nav: {
      fontSize: "1.5rem",
    }
  }
  render() {
    let {styles} = this;
    return(
      <div className='header-desktop'>
        <div style={styles.nav}>
          {LINKS.map(({name, url}) => {
            return <Link href={url} key={url}>
              <DesktopHeaderLink name={name} url={url} selected={url == this.props.router.pathname} />
            </Link>
          })}
        </div>
      </div>
    )
  }
}
DesktopHeader = withRouter(DesktopHeader)

class DesktopHeaderLink extends React.Component {
  styles = {
    linkStyle: {
      marginRight: 15,
      textDecoration: "none",
      color: "white",

    },
    hoverStyle: {
      marginRight: 15,
      textDecoration: "none",
      color: "gray",
    },
    selectedStyle: {
      color:"#ed1c24"
    }
  }
  state = { hover:false }
  handleHover = () => {
    this.setState({hover:!this.state.hover});
  }
  render() {
    let {linkStyle, hoverStyle, selectedStyle} = this.styles
    , {name, url, selected} = this.props
    , {hover} = this.state
    , style = linkStyle;
    if (hover) style = hoverStyle
    if (selected) style = selectedStyle
    return <a onMouseEnter={this.handleHover} onMouseLeave={this.handleHover}
      style={style}>{name}</a>
  }
}
